import { Injectable } from '@angular/core';
import { statesData, territoriesData } from '../../utils/data';
import '../../assets/js/mapData.js';
declare var myExtObject: any;

@Injectable({
  providedIn: 'root'
})
export class DataService {

  constructor() { }

  getAllDetails(c, d) {
    let domainData = myExtObject.getDomainData();
    let states = statesData;
    let territories = territoriesData;
    let b = [];
    let a = [];
    a = Object.entries(domainData['Domain_' + c]);
    a = a.map((x) => {
      return {
        key: x[0],
        value: x[1]
      };
    });
    var l = a.length;
    b = states.filter((val: any) => {
      for (var i = 0; i < l; i++) {
        if (val.key.includes(a[i].key)) {
          val.grade = myExtObject.findGrade(val.key);
          val.score = a[i].value;
          val.max = a[0].value;
          return val;
        }
      }
    });
    territories.filter((val: any) => {
      for (var i = 0; i < l; i++) {
        if (val.key.includes(a[i].key)) {
          val.grade = myExtObject.findGrade(val.key);
          val.score = a[i].value;
          val.max = a[0].value;
          b.push(val);
        }
      }
    });
    if (d) {
      console.log(d);
      return b.sort((x, y) => y.score - x.score).slice(0, 4);
    }
    return b.sort((x, y) => y.score - x.score);
  }
}
