import { Component, OnInit, AfterViewInit } from '@angular/core';
import { domains } from '../../../utils/data';
import { SlidesOutputData, OwlOptions } from 'ngx-owl-carousel-o';
import { statesData, territoriesData } from '../../../utils/data';
import '../../../assets/js/mapData.js';
declare var myExtObject: any;

@Component({
  selector: 'app-national-findings',
  templateUrl: './national-findings.component.html',
  styleUrls: ['./national-findings.component.css']
})
export class NationalFindingsComponent implements OnInit, AfterViewInit {
  domains: any;
  domainData: any;
  arr: any;
  arrAvg: any;
  states: any;
  territories: any;
  dom: any;
  showSlider = 'big';

  bigSlider: OwlOptions = {
    loop: false,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    autoplay: false,
    smartSpeed: 1000,
    dots: false,
    navText: ['', ''],
    responsiveRefreshRate: 100,
    responsive: {
      0: {
        items: 1
      }
    },
    nav: true
  };
  smSlider: OwlOptions = {
    loop: false,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    autoplay: false,
    smartSpeed: 1000,
    dots: false,
    navText: ['', ''],
    responsiveRefreshRate: 100,
    margin: 10,
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 2
      },
      740: {
        items: 3
      },
      940: {
        items: 4
      }
    },
    nav: true
  };
  constructor() { }

  activeSlides: SlidesOutputData;
  getData(data: SlidesOutputData) {
    this.activeSlides = data;
    let items = document.querySelectorAll('.owl-item .progress-meter');
    items.forEach(i => {
      i.removeAttribute('style');
    });
    setTimeout(() => {
      this.loaderAnimation();
    });
  }

  loaderAnimation() {
    let progresslItem = document.querySelectorAll('.owl-item.active .progress-box');
    progresslItem.forEach((item: any) => {
      let performance = item.querySelector('.performance');
      let total = item.querySelector('.total');
      let t = total.textContent;
      let p = performance.textContent;
      let percentage;

      function getPercentage(a, b) {
        percentage = (a / b) * 100;
      }

      getPercentage(p, t);


      function increaseClouser(p) {
        let thisP = p;
        let intervalId = null;

        function clearThisInterval() {
          // console.log(p);
          clearInterval(intervalId);
        }

        return function increase(t) {
          let i = 0;

          intervalId = setInterval(() => {
            performance.textContent = i++;
            if (i > thisP) { clearThisInterval() };
          }, t)
        }
      }

      let increase = increaseClouser(p);
      let progressVal = item.querySelector('.progress-meter');
      let r = 85;
      let c = 2 * Math.PI * r;
      progressVal.style.transition = 'all 2s ease-in-out';
      function progress(value) {
        let progress = value / 100;
        let dashoffset = c * (1 - progress);
        progressVal.style.strokeDashoffset = dashoffset;
      }
      progressVal.style.strokeDasharray = c;
      let calc = Math.round(2000 / p) - 2;
      increase(calc);
      progress(percentage);
    });
  }

  ngOnInit() {
    this.domains = domains;
    this.domainData = myExtObject.getDomainData();
    for (let i = 0; i < this.domains.length; i++) {
      this.arr = Object.values(this.domainData[this.domains[i].key]);
      if (this.arr.indexOf(this.domains[i].max + '') !== -1) {
        this.arr.splice(this.arr.indexOf(this.domains[i].max + ''), 1);
      }
      this.arrAvg = this.arr.reduce(function (a, b) {
        return parseInt(a) + parseInt(b);
      });
      let avg = this.arrAvg / 36;
      this.domains[i].avg = parseInt(avg + '');
      this.domains[i].lowest = Math.min(...this.arr);
      this.domains[i].highest = Math.max(...this.arr);
    }

    this.states = statesData;
    this.territories = territoriesData;
    this.states = JSON.parse(JSON.stringify(this.states));
    for (let i = 0; i < this.states.length; i++) {
      this.states[i].grade = myExtObject.findGrade(this.states[i].key);
      this.states[i].domain = [];
      for (let j = 0; j < domains.length; j++) {
        this.dom = {};
        this.dom.name = domains[j].value;
        this.dom.max = domains[j].max;
        this.dom.icon = domains[j].img;
        this.dom.score = this.domainData[domains[j].key][this.states[i].key];
        this.states[i].domain.push(this.dom);
      }
    }
    for (let i = 0; i < this.territories.length; i++) {
      this.territories[i].grade = myExtObject.findGrade(this.territories[i].key);
      this.territories[i].domain = [];
      for (let j = 0; j < domains.length; j++) {
        this.dom = {};
        this.dom.name = domains[j].value;
        this.dom.max = domains[j].max;
        this.dom.icon = domains[j].img;
        this.dom.score = this.domainData[domains[j].key][this.states[i].key];
        this.territories[i].domain.push(this.dom);
      }
      this.states.push(this.territories[i]);
    }

  }
  ngAfterViewInit() {

  }


}
