import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpParams } from '@angular/common/http';
import { environment } from '../../environments/environment';


@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor(private http: HttpClient) { }

  login(loginModel) {
    loginModel.url = 'users/authenticate/AdminUser';
    return this.http.post<any>(environment.user_service_url + '/users/authenticate/AdminUser', loginModel);
  }

  forgotPass(forgotPassModel) {
    forgotPassModel.url = 'users/forgotPassword/AdminUser';
    return this.http.post<any>(environment.user_service_url + '/users/forgotPassword/AdminUser', forgotPassModel);
  }

  otp(otpModel) {
    otpModel.url = 'users/validate/AdminUser';
    return this.http.post<any>(environment.user_service_url + '/users/validate/AdminUser', otpModel);
  }

  changePassword(userDto) {
    userDto.url = 'users/manage/AdminUser';
    return this.http.post<any>(environment.user_service_url + '/users/manage/AdminUser', userDto);
  }

  resetPassword(userDto) {
    userDto.url = 'users/resetPassword/AdminUser';
    return this.http.post<any>(environment.user_service_url + '/users/resetPassword/AdminUser', userDto);
  }
}
