import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './pgi-web/home/home.component';
import { AboutComponent } from './pgi-web/about/about.component';
import { NationalFindingsComponent } from './pgi-web/national-findings/national-findings.component';
import { DomainComponent } from './pgi-web/domain/domain.component';
import { StateComponent } from './state/state.component';
import { IndicatorsComponent } from './pgi-web/indicators/indicators.component';
import { PgiWebComponent } from './pgi-web/pgi-web.component';
import { PgiAdminComponent } from './pgi-admin/pgi-admin.component';
import { LoginComponent } from './pgi-admin/login/login.component';
import { AdminHomeComponent } from './pgi-admin/admin-home/admin-home.component';
import { AuthGuard } from './guard/auth.guard';
import { ManageRecordsComponent } from './pgi-admin/admin-home/manage-records/manage-records.component';

const routes: Routes = [
  {
    path: '', component: PgiWebComponent, children: [
      { path: '', redirectTo: '/home', pathMatch: 'full' },
      { path: 'home', component: HomeComponent },
      { path: 'about/:id', component: AboutComponent },
      { path: 'national-findings', component: NationalFindingsComponent },
      { path: 'domain/:id', component: DomainComponent },
      { path: 'state/:sId', component: StateComponent },
      { path: 'indicators', component: IndicatorsComponent },
    ]
  },
  {path: 'sandbox', component: PgiAdminComponent, children: [
   
    {path: '', component: LoginComponent},
    {path: 'home', component: AdminHomeComponent, canActivate: [AuthGuard], children: [
      {path: '', component: ManageRecordsComponent}
    ]}
  ]}

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
