import { Component, OnInit, Pipe, Sanitizer, AfterContentInit, AfterViewInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { statesData, domains, gradeData, territoriesData, Domain1, Domain2, Domain3, Domain4, Domain5 } from '../../utils/data';
import '../../assets/js/mapData.js';
declare var myExtObject: any;

@Component({
  selector: 'app-state',
  templateUrl: './state.component.html',
  styleUrls: ['./state.component.css']
})
export class StateComponent implements OnInit, AfterViewInit {
  pageId: any;
  states: any;
  territories: any;
  stateDetail: any;
  grade: any;
  domains: any;
  domainData: any;
  gradeData: any;
  impDomain = [];
  arrAvg: any;
  simStates: any;
  simStateDetails: any;
  domain1: any;
  domain2: any;
  domain3: any;
  domain4: any;
  domain5: any;
  constructor(private route: ActivatedRoute, private sanitizer: Sanitizer) { }

  loaderAnimation() {
    let progresslItem = document.querySelectorAll('.progress-box');
    progresslItem.forEach((item: any) => {
      let performance = item.querySelector('.performance');
      let total = item.querySelector('.total');
      let t = total.textContent;
      let p = performance.textContent;
      let percentage;

      function getPercentage(a, b) {
        percentage = (a / b) * 100;
      }

      getPercentage(p, t);


      function increaseClouser(p) {
        let thisP = p;
        let intervalId = null;

        function clearThisInterval() {
          clearInterval(intervalId);
        }

        return function increase(t) {
          let i = 0;

          intervalId = setInterval(() => {
            performance.textContent = i++;
            if (i > thisP) { clearThisInterval() };
          }, t)
        }
      }

      let increase = increaseClouser(p);
      let progressVal = item.querySelector('.progress-meter');
      let r = 85;
      let c = 2 * Math.PI * r;
      progressVal.style.transition = 'all 2s ease-in-out';
      function progress(value) {
        let progress = value / 100;
        let dashoffset = c * (1 - progress);
        progressVal.style.strokeDashoffset = dashoffset;
      }
      progressVal.style.strokeDasharray = c;
      let calc = Math.round(2000 / p) - 2;
      increase(calc);
      progress(percentage);
    });
  }

  refreshLoader() {
    const items = document.querySelectorAll('.progress-meter');
    items.forEach(i => {
      i.removeAttribute('style');
    });
  }


  ngOnInit() {
    this.states = statesData;
    this.territories = territoriesData;
    this.domains = domains;
    this.gradeData = gradeData;
    this.domain1 = Domain1;
    this.domain2 = Domain2;
    this.domain3 = Domain3;
    this.domain4 = Domain4;
    this.domain5 = Domain5;

    this.route.params.subscribe(param => {
      this.pageId = param[`sId`];

      this.stateDetail = this.states.find(elem => {
        return elem.value === this.pageId;
      });
      if (!this.stateDetail) {
        this.stateDetail = this.territories.find(elem => {
          return elem.value === this.pageId;
        });
      }
      this.grade = myExtObject.findGrade(this.stateDetail.key);
      this.domainData = myExtObject.getDomainData();
      for (var i = 0; i < this.domains.length; i++) {
        this.domains[i].score = this.domainData[this.domains[i].key][this.stateDetail.key]
        let arr = Object.values(this.domainData[this.domains[i].key]);
        let arrAvg = arr.reduce(function (a, b) {
          return parseInt(a + '') + parseInt(b + '')
        });
        let avg = this.arrAvg / 36;
        if (this.domains[i].score < parseInt(avg + '')) {
          this.impDomain.push(this.domains[i]);
        }
      }

      this.simStates = this.gradeData['Grade' + this.grade].filter(value => {
        return (value !== this.stateDetail.key);
      });

      this.simStateDetails = this.states.filter(val => {
        val.grade = myExtObject.findGrade(val.key);
        return this.simStates.includes(val.key);
      });
      this.territories.filter(val => {
        val.grade = myExtObject.findGrade(val.key);
        if (this.simStates.includes(val.key)) {
          this.simStateDetails.push(val);
        }
      });

      this.simStateDetails = this.simStateDetails.slice(0, 3);
      this.refreshLoader();
      setTimeout(() => {
        this.loaderAnimation();
      }, 300);
    });

  }

  ngAfterViewInit() {
    let accordion = document.getElementsByClassName('accordion');
    for (let i = 0; i < accordion.length; i++) {
      accordion[i].addEventListener('click', function () {
        let panel = this.nextElementSibling;
        if (panel.style.maxHeight) {
          panel.style.maxHeight = null;
        } else {
          panel.style.maxHeight = panel.scrollHeight + 'px';
        }
      });
    }
  }
}
