import { Component, OnInit, AfterViewInit } from '@angular/core';

import { OwlOptions } from 'ngx-owl-carousel-o';
import { statesData, territoriesData, gradeData, Domain1, Domain2, Domain3, Domain4, Domain5, domains } from '../../../utils/data';
import { DataService } from '../../services/data-service.service';
import * as CanvasJS from '../../../assets/js/canvas.js';
import '../../../assets/js/mapData.js';
declare var myExtObject: any;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, AfterViewInit {

  slidesStore = [
    { id: 'slide_1', src: 'assets/images/home-banner/banner-1.jpg', alt: 'Learning Outcomes & Quality' },
    { id: 'slide_2', src: 'assets/images/home-banner/banner-2.jpg', alt: 'Access' },
    { id: 'slide_3', src: 'assets/images/home-banner/banner-3.jpg', alt: 'Infrastructure & Facilities' },
    { id: 'slide_4', src: 'assets/images/home-banner/banner-4.jpg', alt: 'Equity' },
    { id: 'slide_5', src: 'assets/images/home-banner/banner-5.jpg', alt: 'Governance Processes' }
  ];

  states: any = [];
  territories: any = [];
  domains: any = [];
  domainData: any;
  topAchiversDetails: any;
  topAchivres: any;
  topDomainAchiver: any;
  topStates: any;
  activeMenu: any;
  indData: any;
  view: any;
  masterCat: any;
  domainCat: any;
  outDomain: any;
  govDomain: any;
  gradeType: any;
  showType: any;
  dName:any;

  constructor(private getData: DataService) { }

  homeBanner: OwlOptions = {
    loop: false,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    autoplay: true,
    autoplayTimeout: 5000,
    smartSpeed: 1000,
    dots: true,
    navText: ['', ''],
    responsiveRefreshRate: 100,
    responsive: {
      0: {
        items: 1
      },
      400: {
        items: 1
      },
      740: {
        items: 1
      },
      940: {
        items: 1
      }
    },
    nav: false
  };

  ngOnInit() {
    // this.mapRefresh();
    this.masterCat = 'G';
    this.gradeType = 0;
    this.states = statesData;
    this.territories = territoriesData;
    this.domains = domains;
    this.domainData = myExtObject.getDomainData();
    this.topAchivres = gradeData.Grade1.concat(gradeData.Grade2).slice(0, 5);
    this.topAchiversDetails = this.states.filter((val: any) => {
      val.grade = myExtObject.findGrade(val.key);
      return this.topAchivres.includes(val.key);
    });
    this.territories.filter((val: any) => {
      val.grade = myExtObject.findGrade(val.key);
      if (this.topAchivres.includes(val.key)) {
        this.topAchiversDetails.push(val);
      }
    });
    this.getDomainData(1);
    this.showTabWise('M');
  }

  ngAfterViewInit() {
    this.mapRefresh();
    this.showGradeWise(0);
    this.getGridWiseChart();
  }

  getGridWiseChart() {
    setTimeout(() => {
      myExtObject.gridWiseChart();
    });
  }

  showSubMenu() {
    if (this.masterCat === 'G') {
      this.gradeType = 0;
      this.getGridWiseChart();
    } else {
      this.domainCat = 'O';
      this.outDomain = 1;
      this.getIndData(1);
    }
    this.showMapOnTime();
    this.showTabWise('M');
  }

  showTabWise(type) {
    this.view = type;
    if (this.masterCat === 'G') {
      if (this.view === 'M') {
        this.gradeType = 0;
        this.showType = "gradeMap";
      }
      else {
        this.showType = "gradeTable";
      }

    }
    else {
      if (this.view === 'M') {
        this.showType = "domMap";
      }
      else {
        setTimeout(() => {
          this.getIndData(1);
        });
        this.domainCat = 'O';
        this.outDomain = 1;
        this.showType = "domTable";

      }
    }
    console.log(this.showType);
  }

  getCatWiseDomains() {
    if (this.domainCat == 'O') {
      this.outDomain = 1;
      this.getIndData(1);
    } else {
      this.govDomain = 5;
      this.getIndData(5);
    }
  }

  getDomainData(d) {
    this.topStates = this.getData.getAllDetails(d, true);
  }

  getIndData(domain) {
    this.activeMenu = '';
    this.indData = (domain == 1 ? Domain1 : (domain == 2 ? Domain2 : (domain == 3 ? Domain3 : (domain == 4 ? Domain4 : Domain5))));
    
    myExtObject.showDomainWise(domain);
    var domainChart = [];
    var domainChartData: any = {};
    var dataPoints = [];

    for (var j = 0; j < this.states.length; j++) {
      dataPoints.push({
        label: this.states[j].key,
        y: parseInt(this.domainData['Domain_' + domain][this.states[j].key])
      });
    }
    for (var j = 0; j < this.territories.length; j++) {
      dataPoints.push({
        label: this.territories[j].key,
        y: parseInt(this.domainData['Domain_' + domain][this.territories[j].key])
      });
    }
    domainChartData.dataPoints = dataPoints;
    domainChart.push(domainChartData);
    domainChartData = {};
    dataPoints = [];
    for (var j = 0; j < this.states.length; j++) {
      dataPoints.push({
        label: this.states[j].key,
        y: this.domainData['Domain_' + domain]['max'] - this.domainData['Domain_' + domain][this.states[j].key]
      });
    }
    for (var i = 0; i < this.territories.length; i++) {
      dataPoints.push({
        label: this.territories[i].key,
        y: this.domainData['Domain_' + domain]['max'] - this.domainData['Domain_' + domain][this.territories[i].key]
      });
    }

    domainChartData.dataPoints = dataPoints;
    domainChart.push(domainChartData);
    // console.log(domainChart);
    for (var i = 0; i < domainChart.length; i++) {

      domainChart[i].type = "stackedBar100";
      this.dName = this.domains.filter(function (val) {
        return (val.key == ("Domain_" + domain));
      });
      domainChart[i].name = this.dName[0].value;
      if (i == 1) {
        domainChart[i].name = 'Distance from max score.';
      }
      domainChart[i].showInLegend = true;

    }

    if (domain == 1) {
      CanvasJS.addColorSet('chartClr', ['#fac090', '#e46c0b']);
    } else if (domain == 2) {
      CanvasJS.addColorSet('chartClr', ['#e5b7b7', '#8165a2']);
    } else if (domain == 3) {
      CanvasJS.addColorSet('chartClr', ['#4298af', '#db843d']);
    } else if (domain == 4) {
      CanvasJS.addColorSet('chartClr', ['#c5bd98', '#31859d']);
    } else {
      CanvasJS.addColorSet('chartClr', ['#da9695', '#595959']);
    }

    let chart1 = new CanvasJS.Chart('chartDomain', {
      animationEnabled: true,
      // theme: 'light1',
      colorSet: 'chartClr',
      title: {
        text: '',
      },
      axisY: {
        interval: 10,
        suffix: '%',
        labelFontSize: 12,
        gridColor: '#efefef',
      },
      axisX: {
        labelFontSize: 10,
        labelAutoFit: true,
        interval: 1,
        gridColor: '#efefef',
        tickColor: '#efefef'
      },
      legend: {
        fontSize: 16,
        fontWeight: 400,
        fontFamily: 'Source Sans Pro, sans-serif',
      },
      toolTip: {
        shared: true
      },
      data: domainChart
    });
    chart1.render();
    myExtObject.hideWatermark();
  }

  showIndicatorWise(ind) {
    this.activeMenu = ind.Serial;
    myExtObject.showIndicatorWise(ind);
  }

  showGradeWise(grade) {
    myExtObject.showGradeWise(grade);

  }

  showMapOnTime() {
    setTimeout(() => {
      if (this.masterCat === 'G') {
        myExtObject.showGradeWise(0);
      }
      else {
        myExtObject.showDomainWise(1);
      }
    });
  }

  mapRefresh() {
    myExtObject.refresh();
  }

}
