import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { statesData, territoriesData } from '../../../utils/data';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit, OnDestroy {
  states: any = [];
  territories: any = [];
  interval: any;

  constructor() { }

  ngOnInit() {

    this.states = statesData;
    this.territories = territoriesData;

    let d = new Date();

    function formatAMPM(date) {
      let hours = date.getHours();
      let minutes = date.getMinutes();
      let ampm = hours >= 12 ? 'PM' : 'AM';
      hours = hours % 12;
      hours = hours ? hours : 12;
      minutes = minutes < 10 ? '0' + minutes : minutes;
      let strTime = hours + ':' + minutes + ' ' + ampm;
      return strTime;
    }
    let months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
    document.getElementById('date').innerHTML = (d.getDate() < 10 ? '0' + d.getDate() : d.getDate()) +
      ' ' + months[
      d.getMonth()] + ' ' + d.getFullYear();
    document.getElementById('time').innerHTML = formatAMPM(d) + ' IST';
    this.interval = setInterval(() => {
      document.getElementById('time').innerHTML = formatAMPM(new Date()) + ' IST';
    }, 1000);

  }


  ngOnDestroy() {
    clearInterval(this.interval);
  }



}
